					font-size: 1.3125em; } }
			tr {
				position: relative;
				&:nth-child(even) {
					background: $alabaster; }
				&:nth-child(2) {
					td {
						padding-top: 35px; } }
				&:last-child {
					border-bottom: 1px solid $wild-sand;
					td {
						padding-bottom: 35px; } } } } } }
